:root {
  --active-color: #00acdc;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--active-color);
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--active-color);
}

.card {
  justify-content: left;
  align-items: left;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
  transition: transform .3s;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 3px;
}

.card__name {
  color: #060b35;
  height: 36px;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 0 16px;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__money {
  justify-content: space-between;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price {
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__numb {
  text-align: right;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  background-color: #00acdc;
  border-color: #00acdc;
  border-radius: 5px;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
}

.card__button-link {
  color: #fff;
  text-underline-position: from-font;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button .card__button-link:hover {
  color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__info .card__name {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
  text-underline-position: from-font;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  width: 100%;
  min-width: 944px;
  margin: 0 auto;
  padding-top: 50px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1024px) and (width >= 1200px) {
  .container {
    margin-left: 0 auto;
    grid-template-columns: repeat(4, 200px);
  }
}
